      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="每人投资金额">
            <el-input size="small" placeholder="请输入内容" v-model="form.per_money">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="五级佣金">
            <el-input size="small" placeholder="请输入内容" v-model="form.five_apr">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="四级佣金">
            <el-input size="small" placeholder="请输入内容" v-model="form.four_apr">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="三级佣金">
            <el-input size="small" placeholder="请输入内容" v-model="form.three_apr">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="二级佣金">
            <el-input size="small" placeholder="请输入内容" v-model="form.two_apr">
              <template slot="append">%</template>
