<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      form: {
        card: '',
        code: '',
        name: '',
      },
      config: {
        bankList: [],
      },
    }
  },
  mounted() {
    this.getInfo()
  },
  methods: {
    getInfo() {
      console.log('id->', id)
      const id = this.$router.history.current.params.id
      const url = id > 0 ? '/user/team/edit/' + id : '/user/level/config'
      this.loading = true

      if (id) {
        Fetch2(url, { id: id })
          .then(r => {
            this.loading = false
            if (r.data.view) {
              this.form = r.data.view
              this.config.bankList = r.data.config.code
            } else {
              this.config.bankList = r.data.code
            }
          })
          .catch(() => (this.loading = false))
      } else {
        Fetch(url, { id: id })
          .then(r => {
            this.loading = false
            if (r.data.view) {
              this.form = r.data.view
              this.config.bankList = r.data.config.code
            } else {
              this.config.bankList = r.data.code
            }
          })
          .catch(() => (this.loading = false))
      }
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      this.loading = true
      if (id) {
        Fetch3('/user/team/edit/' + id, {
          id: this.$router.history.current.params.id,
          ...this.form,
        })
          .then(r => {
            this.loading = false
            Message.success(r.msg)
            this.$router.replace('/member/team')
          })
          .catch(() => (this.loading = false))
      } else {
        Fetch('/user/team/create', {
          id: this.$router.history.current.params.id,
          ...this.form,
        })
          .then(r => {
            this.loading = false
            Message.success(r.msg)
            this.$router.replace('/member/team')
          })
          .catch(() => (this.loading = false))
      }
    },
    bankChange() {
      this.form.name = this.config.bankList[this.form.code]
    },
  },
}
</script>
